@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}*{font-family:'Roboto', sans-serif}body{background-color:#737373}.container{max-width:1280px;margin:0 auto}

.hero{background-image:linear-gradient(to top, rgba(0,0,0,0.5), rgba(0,0,0,0.5)),url(/air-quality/static/media/hero-bg.06b732d1.jpg);background-size:cover;padding:80px 0 180px}.hero-partners{display:flex;align-items:center}.hero-partners-item{margin-left:60px}.hero-partners-item:first-child{margin-left:0}.hero-navigation{margin-top:45px}.hero-navigation-item{text-decoration:none;font-weight:normal;font-size:20px;line-height:20px;color:#fff;margin-left:75px}.hero-navigation-item:first-child{margin-left:0}.hero-title{width:665px;font-weight:normal;font-size:55px;line-height:65px;color:#fff;margin:110px 0 130px}.hero-link{display:inline-block;padding:35px 150px;text-decoration:none;font-weight:normal;font-size:30px;line-height:35px;color:#fff;background:#B78A3F}

.facts{position:relative;box-sizing:border-box;padding:100px 60px 50px}.facts-title{font-weight:normal;font-size:35px;line-height:40px;text-align:center;color:#fff;margin-bottom:30px}.facts-slide-desc{font-weight:normal;font-size:25px;line-height:30px;text-align:center;color:#fff;margin-bottom:40px}.facts>.swiper-button-prev,.facts .swiper-button-next{color:#B78A3F}.facts>.swiper-button-prev{left:10px}.facts>.swiper-button-next{right:10px}.facts>.swiper-button-disabled{opacity:0}.facts-pagination{display:flex;justify-content:center;margin-top:65px}.facts-pagination-bullet{display:inline-block;width:25px;height:25px;border-radius:50%;margin:0 20px;background-color:transparent;border:2px solid #B78A3F;opacity:1;transition:0.3s background-color}.facts-pagination>.swiper-pagination-bullet-active{background-color:#B78A3F}

.footer{border-top:1px solid #fff}.footer-partners{display:flex;align-items:center}.footer-partners-item{margin-right:120px}.footer-partners-item:last-child{margin-right:0}.footer-text-wrap{display:flex;justify-content:space-between}.footer-nav{display:flex;flex-direction:column;margin-bottom:90px}.footer-nav-link{font-weight:normal;font-size:15px;line-height:20px;color:#fff;text-decoration:none;margin-bottom:20px}.footer-nav-link:last-child{margin-bottom:0}.footer-nav-text{width:365px;font-weight:normal;font-size:15px;line-height:20px;color:#fff}.footer-text{font-weight:300;font-size:15px;line-height:15px;color:#fff;margin-bottom:14px}.text-yellow{color:#ede852}

.sectors{padding:50px 0}.sectors-title{color:#fff;font-size:35px;font-weight:normal;line-height:40px;text-align:center;margin-bottom:40px}.sectors-subtitle{font-weight:normal;font-size:25px;line-height:25px;color:#fff;margin-bottom:30px}.sectors-list{display:flex;flex-wrap:wrap;justify-content:space-between}.sectors-list-item{position:relative;width:calc((100% / 3) - 70px);height:240px;display:flex;align-items:center;margin-bottom:30px}.sectors-list-item-tooltip{width:440px;position:absolute;left:100%;pointer-events:none;-webkit-appearance:none;-moz-appearance:none;appearance:none;background-color:#DADADA;padding:16px 16px 16px 75px;-webkit-clip-path:polygon(75px 0, 100% 0, 100% 100%, 75px 100%, 0 50%);clip-path:polygon(75px 0, 100% 0, 100% 100%, 75px 100%, 0 50%);z-index:1;-webkit-animation:fadeIn 0.4s ease-in-out;animation:fadeIn 0.4s ease-in-out}.sectors-list-item-tooltip-switched{left:auto;left:initial;right:100%;padding:16px 75px 16px 16px;-webkit-clip-path:polygon(80% 1%, 100% 50%, 80% 100%, 0 100%, 0 0);clip-path:polygon(80% 1%, 100% 50%, 80% 100%, 0 100%, 0 0)}.sectors-list-item-name{width:100%;text-align:center;font-size:25px;font-weight:30px;color:#222222;background-color:rgba(255,255,255,0.83)}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}

.air-q{padding:50px 0}.air-q-map{height:560px;margin:40px 0}.air-q-title{font-weight:normal;font-size:35px;line-height:40px;text-align:center;color:#fff}.air-q-text{font-weight:normal;font-size:25px;line-height:25px;color:#fff}

